.header__button {
  height: 22px;
  padding: 0;
  border: none;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  color: #fff;
  background-color: #000;
  text-decoration: none;
  transition: opacity .1s linear;
  cursor: pointer;
}

.header__button:hover {
  opacity: .6;
}